import {
  apiValidator as commonApiValidator,
  messages,
  BulkError,
  wdeValidationError,
} from '../errors'
import { HttpApiClientFactory } from '../types'
import * as apiTypes from '../types/data-item-types'

import { hasField, isObject, isArray } from '../utils'
import { filterBuilder } from './common'
import {
  Provider,
  WixDataBulkResult,
  WixDataItem,
  WixDataItemOrId,
  WixDataOptions,
  WixDataPatchOptions,
  WixDataQueryReferencedOptions,
  WixDataReadOptions,
  WixDataReadWithProjectionOptions,
  WixDataReference,
  WixDataUpdateOptions,
} from './types'
import { QueryParams, WixDataQueryImpl } from './impl/WixDataQueryImpl'
import { WixDataQuery } from './WixDataQuery'
import { WixDataResultImpl, PageFetcher } from './impl/WixDataResultImpl'
import { WixDataAggregateImpl } from './impl/WixDataAggregateImpl'
import { ApiClient } from './ApiClient'
import {
  WixDataPatch,
  FieldModification,
  WixDataBulkPatch,
} from './WixDataPatch'
import { WixDataAggregate } from './WixDataAggregate'
import { WixDataFilter } from './WixDataFilter'
import { WixDataResult } from './WixDataResult'
import { WixDataBulkPatchImpl, WixDataPatchImpl } from './impl/WixDataPatchImpl'
import { WixDataSearch } from './WixDataSearch'
import { WixDataSearchImpl } from './impl/WixDataSearchImpl'
import { WixDataAggregatePipeline } from './WixDataAggregatePipeline'
import { WixDataAggregatePipelineImpl } from './impl/WixDataAggregatePipelineImpl'
import { PipelineStage } from './stages/stages'

type ProvidedOpt<T> = T | undefined | Provider<T | undefined>
type TraceWith = (
  label: string,
  opts?: object
) => <T>(fn: () => Promise<T>) => Promise<T>

export class WixDataApi {
  private client: ApiClient

  constructor(
    clientFactory: HttpApiClientFactory<any>,
    allowGet: boolean,
    private readonly environment?: apiTypes.Environment,
    private readonly gridAppId?: ProvidedOpt<string>,
    private readonly tracer?: TraceWith
  ) {
    this.client = new ApiClient(clientFactory, allowGet)
  }

  get wixData(): WixDataApi {
    return this
  }

  get filter(): WixDataFilter {
    return filterBuilder()
  }

  // NOTE sort method for builder is not implemented

  truncate = withArgs(
    async (
      args,
      collectionName: string,
      options?: WixDataOptions
    ): Promise<void> => {
      await apiValidator()
        .arity('truncate', args, 1, 2)
        .collectionName(collectionName)
        .options(options)
        .validateAndReject()

      return this.trace('truncate', { collectionName })(async (env) => {
        await this.client.truncateDataItems({
          ...env,
          dataCollectionId: collectionName,
          ...toOptions(options),
        })
      })
    }
  )

  // --- single-item methods ---

  get = withArgs(
    async (
      args,
      collectionName: string,
      itemId: string,
      options?: WixDataReadWithProjectionOptions
    ): Promise<WixDataItem | null> => {
      await apiValidator()
        .arity('get', args, 2, 3)
        .collectionName(collectionName)
        .itemId(itemId)
        .options(options)
        .validateAndReject()

      return this.trace('get', { collectionName, itemId })(async (env) => {
        const { dataItem } = await this.client
          .getDataItem({
            ...env,
            dataCollectionId: collectionName,
            dataItemId: itemId,
            ...toReadOptions(options),
            fields: options?.fields,
          })
          .catch(
            recover(ItemDoesNotExistCode, {} as apiTypes.GetDataItemResponse)
          )

        return dataItem ? toDataItem(dataItem) : null
      })
    }
  )

  insert = withArgs(
    async (
      args,
      collectionName: string,
      item: Partial<WixDataItem>,
      options?: WixDataUpdateOptions
    ): Promise<WixDataItem> => {
      await apiValidator()
        .arity('insert', args, 2, 3)
        .collectionName(collectionName)
        .item(item, collectionName, false)
        .options(options)
        .validateAndReject()

      warnAboutBrokenFields(item)

      return this.trace('insert', { collectionName })(async (env) => {
        const { dataItem } = await this.client.insertDataItem({
          ...env,
          dataCollectionId: collectionName,
          dataItem: {
            id: item._id,
            data: item,
          },
          ...toUpdateOptions(options),
        })

        return toDataItem(dataItem!)
      })
    }
  )

  save = withArgs(
    async (
      args,
      collectionName: string,
      item: Partial<WixDataItem>,
      options?: WixDataUpdateOptions
    ): Promise<WixDataItem> => {
      await apiValidator()
        .arity('save', args, 2, 3)
        .collectionName(collectionName)
        .item(item, collectionName, false)
        .options(options)
        .validateAndReject()

      return this.trace('save', { collectionName })(async (env) => {
        const { dataItem } = await this.client.saveDataItem({
          ...env,
          dataCollectionId: collectionName,
          dataItem: {
            id: item._id,
            data: item,
          },
          ...toUpdateOptions(options),
        })
        return toDataItem(dataItem!)
      })
    }
  )

  update = withArgs(
    async (
      args,
      collectionName: string,
      item: WixDataItem,
      options?: WixDataUpdateOptions
    ) => {
      await apiValidator()
        .arity('update', args, 2, 3)
        .collectionName(collectionName)
        .item(item, collectionName, false)
        .options(options)
        .validateAndReject()

      return this.trace('update', { collectionName })(async (env) => {
        const { dataItem } = await this.client.updateDataItem({
          ...env,
          dataCollectionId: collectionName,
          dataItem: {
            id: item._id,
            data: item,
          },
          ...toUpdateOptions(options),
        })
        return toDataItem(dataItem!)
      })
    }
  )

  remove = withArgs(
    async (
      args,
      collectionName: string,
      itemId: string,
      options?: WixDataOptions
    ): Promise<WixDataItem | null> => {
      await apiValidator()
        .arity('remove', args, 2, 3)
        .collectionName(collectionName)
        .itemId(itemId)
        .options(options)
        .validateAndReject()

      return this.trace('remove', { collectionName, itemId })(async (env) => {
        const { dataItem } = await this.client
          .removeDataItem({
            ...env,
            dataCollectionId: collectionName,
            dataItemId: itemId,
            ...toOptions(options),
          })
          .catch(
            recover(ItemDoesNotExistCode, {} as apiTypes.GetDataItemResponse)
          )
        return dataItem ? toDataItem(dataItem) : null
      })
    }
  )

  private toFieldModificationApi = (fieldModification: FieldModification) => {
    switch (fieldModification.action) {
      case 'SET_FIELD':
        return {
          action: apiTypes.ACTION.SET_FIELD,
          fieldPath: fieldModification.fieldPath,
          setFieldOptions: {
            value: fieldModification.actionOptions,
          },
        }
      case 'REMOVE_FIELD':
        return {
          action: apiTypes.ACTION.REMOVE_FIELD,
          fieldPath: fieldModification.fieldPath,
        }
      case 'INCREMENT_FIELD':
        return {
          action: apiTypes.ACTION.INCREMENT_FIELD,
          fieldPath: fieldModification.fieldPath,
          incrementFieldOptions: {
            value: fieldModification.actionOptions,
          },
        }
      case 'APPEND_TO_ARRAY':
        return {
          action: apiTypes.ACTION.APPEND_TO_ARRAY,
          fieldPath: fieldModification.fieldPath,
          appendToArrayOptions: {
            value: fieldModification.actionOptions,
          },
        }
      case 'REMOVE_FROM_ARRAY':
        return {
          action: apiTypes.ACTION.REMOVE_FROM_ARRAY,
          fieldPath: fieldModification.fieldPath,
          removeFromArrayOptions: {
            value: fieldModification.actionOptions,
          },
        }
      default:
        throw new Error('Invalid patch action')
    }
  }

  patch = (collectionName: string, itemId: string): WixDataPatch => {
    return new WixDataPatchImpl({
      collectionName,
      itemId,
      onRun: async (_args, patchParams, options) => {
        const fieldModifications = patchParams.fieldModifications.map(
          this.toFieldModificationApi
        )

        return this.trace('patch', { collectionName })(async (env) => {
          const result = await this.client.patchDataItem({
            ...env,
            dataCollectionId: patchParams.collectionName,
            patch: {
              dataItemId: patchParams.itemId,
              fieldModifications,
            },
            ...toPatchOptions(options),
          })

          return result.dataItem ? toDataItem(result.dataItem) : null
        })
      },
    })
  }

  bulkPatch = (collectionName: string, itemIds: string[]): WixDataBulkPatch => {
    return new WixDataBulkPatchImpl({
      collectionName,
      itemIds,
      onRun: async (_args, patchParams, options) => {
        const fieldModifications = patchParams.fieldModifications.map(
          this.toFieldModificationApi
        )

        return this.trace('bulkPatch', { collectionName })(async (env) => {
          const { results } = await this.client.bulkPatchDataItems({
            ...env,
            dataCollectionId: patchParams.collectionName,
            patches: patchParams.itemIds.map((itemId) => ({
              dataItemId: itemId,
              fieldModifications,
            })),
            ...toPatchOptions(options),
          })

          return toBulkResult(itemIds, results!, ['WDE0073'])
        })
      },
    })
  }

  // --- query, count, distinct ---

  query = (collectionName: string): WixDataQuery => {
    const ensureValidQuery = (invalidArgs: string[]) => {
      if (invalidArgs.length > 0) {
        throw wdeValidationError(
          messages.queryValidations.queryInvalid(collectionName, invalidArgs)
        )
      }
    }

    return new WixDataQueryImpl({
      collectionName,
      onCount: async (args, params, options) => {
        ensureValidQuery([
          ...params.invalidArguments,
          ...apiValidator()
            .collectionName(collectionName)
            .arity('count', args, 0, 1)
            .options(options)
            .validateAndReturn(),
        ])

        return this.trace('count', { collectionName })(async (env) => {
          const { totalCount } = await this.client.countDataItems({
            ...env,
            dataCollectionId: collectionName,
            filter: params.filterTree,
            ...toReadOptions(options),
          })
          return totalCount!
        })
      },
      onDistinct: async (args, params, field, options) => {
        ensureValidQuery([
          ...params.invalidArguments,
          ...apiValidator()
            .collectionName(collectionName)
            .arity('distinct', args, 1, 2)
            .fieldName(field)
            .options(options)
            .validateAndReturn(),
        ])

        const order = toSort(params.orderBy).find(
          (o) => o.fieldName === field
        )?.order

        const fetch: PageFetcher<any> = (cursorOrOffset, returnTotalCount) =>
          this.trace('distinct', { collectionName, field })(async (env) => {
            const { distinctValues, pagingMetadata } =
              await this.client.queryDistinctValues({
                ...env,
                dataCollectionId: collectionName,
                ...toReadOptions(options),
                ...toPaging(params.limitNumber, cursorOrOffset),
                ...(isOffset(cursorOrOffset)
                  ? {
                      fieldName: field,
                      filter: params.filterTree,
                      order,
                      returnTotalCount,
                    }
                  : {}),
              })
            return [distinctValues!, pagingMetadata!]
          })

        const [items, paging] = await fetch(
          params.skipNumber,
          options?.returnTotalCount
        )

        return new WixDataResultImpl(items, paging, fetch, params)
      },
      onFind: async (args, params, options) => {
        ensureValidQuery([
          ...params.invalidArguments,
          ...apiValidator()
            .collectionName(collectionName)
            .arity('find', args, 0, 1)
            .options(options)
            .validateAndReturn(),
        ])

        const fetch: PageFetcher = (cursorOrOffset, returnTotalCount) =>
          this.trace('query', { collectionName })(async (env) => {
            const { dataItems, pagingMetadata } =
              await this.client.queryDataItems({
                ...env,
                dataCollectionId: collectionName,
                query: {
                  fields: params.projectedFields,
                  ...toPaging(params.limitNumber, cursorOrOffset),
                  ...(isOffset(cursorOrOffset)
                    ? {
                        filter: params.filterTree,
                        sort: toSort(params.orderBy),
                      }
                    : {}),
                },
                referencedItemOptions: params.included,
                ...toReadOptions(options),
                ...(isOffset(cursorOrOffset) ? { returnTotalCount } : {}),
              })
            return [dataItems!.map(toDataItem), pagingMetadata!]
          })
        const [items, paging] = await fetch(
          params.skipNumber,
          options?.returnTotalCount
        )

        return new WixDataResultImpl(items, paging, fetch, params)
      },
    })
  }

  search = (collectionName: string): WixDataSearch => {
    const ensureValidQuery = (invalidArgs: string[]) => {
      if (invalidArgs.length > 0) {
        throw wdeValidationError(
          messages.queryValidations.queryInvalid(collectionName, invalidArgs)
        )
      }
    }

    return new WixDataSearchImpl({
      collectionName,
      onRun: async (
        params,
        options?: WixDataReadOptions
      ): Promise<WixDataResult> => {
        ensureValidQuery([
          ...params.invalidArguments,
          ...apiValidator()
            .collectionName(collectionName)
            .options(options)
            .validateAndReturn(),
        ])

        const expression = params.queryText

        const fetch: PageFetcher = (cursorOrOffset) =>
          this.trace('search', { collectionName, expression })(async (env) => {
            const request: apiTypes.SearchDataItemsRequest = {
              ...env,
              dataCollectionId: collectionName,
              search: {
                filter: params.filterTree,
                sort: toSort(params.orderBy),
                fields: params.projectedFields,
                search: {
                  expression: params.queryText,
                  fuzzy: params.isFuzzy,
                  mode: params.searchMode,
                },
                ...toPaging(params.limitNumber, cursorOrOffset),
              },
              referencedItemOptions: params.included,
              ...toReadOptions(options),
            }

            const { dataItems, pagingMetadata } =
              await this.client.searchDataItems(request)
            return [dataItems!.map(toDataItem), pagingMetadata!]
          })

        const [items, paging] = await fetch(params.skipNumber)

        return new WixDataResultImpl(items, paging, fetch, params)
      },
    })
  }

  // NOTE find method is not implemented

  fetch = withArgs(
    async (
      args,
      collectionName: string,
      cursor: string,
      limit?: number,
      options?: WixDataReadOptions
    ): Promise<WixDataResult> => {
      await apiValidator()
        .arity('fetch', args, 2, 4)
        .collectionName(collectionName)
        .isNonEmptyString(cursor, 'cursor')
        .validateAndReject()

      const fetch: PageFetcher = (cursorOrOffset) =>
        this.trace('query', { collectionName })(async (env) => {
          const { dataItems, pagingMetadata } =
            await this.client.queryDataItems({
              ...env,
              dataCollectionId: collectionName,
              query: {
                ...toPaging(limit, cursorOrOffset),
              },
              ...toReadOptions(options),
            })
          return [dataItems!.map(toDataItem), pagingMetadata!]
        })
      const [items, paging] = await fetch(cursor)

      return new WixDataResultImpl(items, paging, fetch, {
        limitNumber: limit,
        collectionName,
        skipNumber: 0,
      })
    }
  )

  // --- aggregate ---

  aggregate = (collectionName: string): WixDataAggregate => {
    return new WixDataAggregateImpl({
      collectionName,
      onRun: async (args, params, options) => {
        const validationErrors = [
          ...params.invalidArguments,
          ...apiValidator()
            .arity('run', args, 0, 1)
            .collectionName(collectionName)
            .options(options)
            .validateAndReturn(),
        ]

        if (validationErrors.length > 0) {
          throw wdeValidationError(
            messages.aggregateValidations.aggregateInvalid(
              collectionName,
              validationErrors
            )
          )
        }

        const fetch: PageFetcher<Record<string, any>> = (
          cursorOrOffset,
          returnTotalCount
        ) =>
          this.trace('aggregate', { collectionName })(async (env) => {
            const toOperation = (field?: string) =>
              field ? { itemFieldName: field } : undefined

            const { results, pagingMetadata } =
              await this.client.aggregateDataItems({
                ...env,
                dataCollectionId: collectionName,
                ...toPaging(params.limitNumber, cursorOrOffset),
                ...toReadOptions(options),
                ...(isOffset(cursorOrOffset)
                  ? {
                      initialFilter: params.filterTree,
                      aggregation: {
                        groupingFields: params.groupBy,
                        operations: params.aggregates.map((a) => ({
                          resultFieldName: a.name,
                          average: toOperation(a.avg),
                          min: toOperation(a.min),
                          max: toOperation(a.max),
                          sum: toOperation(a.sum),
                          itemCount: a.count ? {} : undefined,
                        })),
                      },
                      finalFilter: params.havingTree,
                      sort: toSort(params.orderBy),
                      returnTotalCount,
                    }
                  : {}),
              })
            return [unwrapAggregationId(results!), pagingMetadata!]
          })

        const [items, paging] = await fetch(
          params.skipNumber,
          options?.returnTotalCount
        )

        return new WixDataResultImpl(items, paging, fetch, params)
      },
    })
  }

  fetchAggregate = withArgs(
    async (
      args,
      collectionName: string,
      cursor: string,
      limit?: number,
      options?: WixDataReadOptions
    ) => {
      await apiValidator()
        .arity('fetchAggregate', args, 2, 4)
        .collectionName(collectionName)
        .isNonEmptyString(cursor, 'cursor')
        .validateAndReject()

      const fetch: PageFetcher<Record<string, any>> = (cursorOrOffset) =>
        this.trace('aggregate', { collectionName })(async (env) => {
          const { results, pagingMetadata } =
            await this.client.aggregateDataItems({
              ...env,
              dataCollectionId: collectionName,
              ...toPaging(limit, cursorOrOffset),
              ...toReadOptions(options),
            })
          return [unwrapAggregationId(results!), pagingMetadata!]
        })

      const [items, paging] = await fetch(cursor, true)

      return new WixDataResultImpl(items, paging, fetch, {
        limitNumber: limit,
        skipNumber: 0,
        collectionName,
      })
    }
  )

  aggregatePipeline = (
    collectionName: string,
    pipeline: PipelineStage[]
  ): WixDataAggregatePipeline => {
    return new WixDataAggregatePipelineImpl(
      collectionName,
      pipeline,
      this.client
    )
  }

  // --- bulk methods ---

  bulkRemove = withArgs(
    async (
      args,
      collectionName: string,
      itemIds: string[],
      options?: WixDataOptions
    ): Promise<WixDataBulkResult> => {
      await apiValidator()
        .arity('bulkRemove', args, 2, 3)
        .collectionName(collectionName)
        .itemIds(itemIds, collectionName)
        .bulkRemoveOptions(options)
        .validateAndReject()

      return this.trace('bulkRemove', { collectionName })(async (env) => {
        const { results } = await this.client.bulkRemoveDataItems({
          ...env,
          dataCollectionId: collectionName,
          dataItemIds: itemIds,
          ...toOptions(options),
        })
        // Non-existing items are skipped and not reported as errors.
        return toBulkResult(itemIds, results!, ['WDE0073'])
      })
    }
  )

  private async runBulkSave(
    env: { environment?: apiTypes.Environment; appId?: string },
    collectionName: string,
    items: Partial<WixDataItem>[],
    options?: WixDataOptions,
    overrideExisting: boolean = true
  ): Promise<WixDataBulkResult> {
    const request: apiTypes.BulkInsertDataItemsRequest = {
      ...env,
      dataCollectionId: collectionName,
      dataItems: items.map((data) => ({
        id: data._id,
        data,
      })),
      ...toOptions(options),
    }
    const { results } = await (overrideExisting
      ? this.client.bulkSaveDataItems(request)
      : this.client.bulkInsertDataItems(request))
    const ignoreCodes = overrideExisting ? [] : ['WDE0074']
    return toBulkResult(items, results!, ignoreCodes)
  }

  bulkInsert = withArgs(
    async (
      args,
      collectionName: string,
      items: Partial<WixDataItem>[],
      options?: WixDataOptions & { overrideExisting?: boolean }
    ): Promise<WixDataBulkResult> => {
      await apiValidator()
        .arity('bulkInsert', args, 2, 3)
        .items(items, collectionName)
        .bulkInsertOptions(options)
        .collectionName(collectionName)
        .validateAndReject()

      return this.trace('bulkInsert', { collectionName, options })(
        async (appId) =>
          this.runBulkSave(
            appId,
            collectionName,
            items,
            options,
            options?.overrideExisting ?? false
          )
      )
    }
  )

  bulkSave = withArgs(
    async (
      args,
      collectionName: string,
      items: Partial<WixDataItem>[],
      options?: WixDataOptions
    ): Promise<WixDataBulkResult> => {
      await apiValidator()
        .arity('bulkSave', args, 2, 3)
        .bulkInsertOptions(options)
        .collectionName(collectionName)
        .items(items, collectionName)
        .validateAndReject()

      return this.trace('bulkSave', { collectionName })(async (env) =>
        this.runBulkSave(env, collectionName, items, options)
      )
    }
  )

  bulkUpdate = withArgs(
    async (
      args,
      collectionName: string,
      items: WixDataItem[],
      options?: WixDataOptions
    ): Promise<WixDataBulkResult> => {
      await apiValidator()
        .arity('bulkUpdate', args, 2, 3)
        .bulkUpdateOptions(options)
        .collectionName(collectionName)
        .items(items, collectionName)
        .validateAndReject()

      return this.trace('bulkUpdate', { collectionName })(async (env) => {
        const { results } = await this.client.bulkUpdateDataItems({
          ...env,
          dataCollectionId: collectionName,
          dataItems: items.map((data) => ({
            id: data._id,
            data,
          })),
          ...toOptions(options),
        })
        // Non-existing items are skipped and not reported as errors.
        return toBulkResult(items, results!, ['WDE0073'])
      })
    }
  )

  // --- references ---

  fetchReferenced = withArgs(
    async (
      args,
      collectionName: string,
      cursor: string,
      limit?: number,
      options?: WixDataReadWithProjectionOptions
    ): Promise<WixDataResult<WixDataItem | string>> => {
      await apiValidator()
        .arity('fetchReferenced', args, 2, 4)
        .isNonEmptyString(cursor, 'cursor')
        .collectionName(collectionName)
        .validateAndReject()

      const fetch: PageFetcher<WixDataItem | string> = (current) =>
        this.trace('fetchReferenced', { collectionName })(async (env) => {
          const { results, pagingMetadata } =
            await this.client.queryReferencedDataItems({
              ...env,
              ...toPaging(limit, current),
              ...toReadOptions(options),
              dataCollectionId: collectionName,
              fields: options?.fields,
            })
          return [onlyReferencedItems(results!), pagingMetadata!]
        })

      const [items, paging] = await fetch(cursor)
      return new WixDataResultImpl(items, paging, fetch, {
        collectionName,
        limitNumber: limit,
        skipNumber: 0,
      })
    }
  )

  queryReferenced = withArgs(
    async (
      args,
      collectionName: string,
      holdingItem: WixDataItemOrId,
      relationshipAttribute: string,
      options?: WixDataQueryReferencedOptions
    ): Promise<WixDataResult> => {
      await apiValidator()
        .arity('queryReferenced', args, 3, 4)
        .collectionName(collectionName)
        .referenceParameter(holdingItem)
        .isNonEmptyString(relationshipAttribute, 'relationshipAttribute')
        .options(options)
        .validateAndReject()

      const id = itemId(holdingItem)
      const fetch: PageFetcher = (cursorOrOffset, returnTotalCount) =>
        this.trace('queryReferenced', {
          collectionName,
          itemId: id,
          options,
          relationshipAttribute,
        })(async (env) => {
          const { results, pagingMetadata } =
            await this.client.queryReferencedDataItems({
              ...env,
              ...toPaging(options?.limit ?? 50, cursorOrOffset),
              ...toReadOptions(options),
              dataCollectionId: collectionName,
              fields: options?.fields,
              ...(isOffset(cursorOrOffset)
                ? {
                    referringItemId: id,
                    referringItemFieldName: relationshipAttribute,
                    order:
                      options?.order === 'asc'
                        ? apiTypes.SortOrder.ASC
                        : options?.order === 'desc'
                        ? apiTypes.SortOrder.DESC
                        : undefined,
                    returnTotalCount,
                  }
                : {}),
            })
          return [onlyReferencedItems(results!), pagingMetadata!]
        })

      const [items, paging] = await fetch(
        options?.skip ?? 0,
        options?.returnTotalCount
      )
      return new WixDataResultImpl(items, paging, fetch, {
        collectionName,
        limitNumber: options?.limit ?? 50,
        skipNumber: options?.skip ?? 0,
      })
    }
  )

  insertReference: {
    // overload with array of references
    (
      collectionName: string,
      refs: WixDataReference[],
      options?: WixDataOptions
    ): Promise<void>
    // overload with separate left and right(s)
    (
      collectionName: string,
      relationshipAttribute: string,
      left: WixDataItemOrId,
      right: WixDataItemOrId | WixDataItemOrId[],
      options?: WixDataOptions
    ): Promise<void>
  } = withArgs(
    async (
      args,
      collectionName: string,
      refsOrAttr: any,
      leftOrOpts?: any,
      right?: any,
      options?: any
    ) => {
      let refs: WixDataReference[]
      let opts: WixDataOptions
      let argc: [number, number]

      if (typeof refsOrAttr === 'string') {
        const rights: WixDataItemOrId[] = asArray(right)
        refs = rights.map((r) => ({
          relationshipName: refsOrAttr,
          left: leftOrOpts as WixDataItemOrId,
          right: r,
        }))
        opts = options
        argc = [4, 5]
      } else {
        refs = refsOrAttr
        opts = leftOrOpts
        argc = [2, 3]
      }
      await apiValidator()
        .arity('insertReference', args, ...argc)
        .collectionName(collectionName)
        .references(refs)
        .options(opts)
        .validateAndReject()

      await this.trace('insertReference', { collectionName })(async (env) =>
        this.client.bulkInsertDataItemReferences({
          ...env,
          dataCollectionId: collectionName,
          dataItemReferences: refs.map((r) => ({
            referringItemFieldName: r.relationshipName,
            referringItemId: itemId(r.left),
            referencedItemId: itemId(r.right),
          })),
          ...toOptions(opts),
        })
      )
    }
  )

  replaceReferences = withArgs(
    async (
      args,
      collectionName: string,
      relationshipAttribute: string,
      left: WixDataItemOrId,
      right: WixDataItemOrId | WixDataItemOrId[],
      options?: WixDataOptions
    ): Promise<void> => {
      const rights = asArray(right)
      await apiValidator()
        .arity('replaceReferences', args, 4, 5)
        .collectionName(collectionName)
        .isNonEmptyString(relationshipAttribute, 'relationshipAttribute')
        .referenceParameter(left)
        .referenceParameters(rights)
        .options(options)
        .validateAndReject()

      await this.trace('replaceReferences', {
        collectionName,
        relationshipAttribute,
      })(async (env) =>
        this.client.replaceDataItemReferences({
          ...env,
          dataCollectionId: collectionName,
          referringItemFieldName: relationshipAttribute,
          referringItemId: itemId(left),
          newReferencedItemIds: rights.map(itemId),
          ...toOptions(options),
        })
      )
    }
  )

  removeReference = withArgs(
    async (
      args,
      collectionName: string,
      relationshipAttribute: string,
      left: WixDataItemOrId,
      right: WixDataItemOrId | WixDataItemOrId[],
      options?: WixDataOptions
    ) => {
      const rights = asArray(right)
      await apiValidator()
        .arity('removeReference', args, 4, 5)
        .collectionName(collectionName)
        .referenceParameter(left)
        .referenceRemoveParameters(rights)
        .isNonEmptyString(relationshipAttribute, 'relationshipAttribute')
        .options(options)
        .validateAndReject()

      await this.trace('removeReference', {
        collectionName,
        relationshipAttribute,
      })(async (env) =>
        this.client.bulkRemoveDataItemReferences({
          ...env,
          dataCollectionId: collectionName,
          dataItemReferences: rights.map((r) => ({
            referringItemFieldName: relationshipAttribute,
            referringItemId: itemId(left),
            referencedItemId: itemId(r),
          })),
          ...toOptions(options),
        })
      )
    }
  )

  isReferenced = withArgs(
    async (
      args,
      collectionName: string,
      relationshipAttribute: string,
      left: WixDataItemOrId,
      right: WixDataItemOrId,
      options?: WixDataReadOptions
    ): Promise<boolean> => {
      await apiValidator()
        .arity('isReferenced', args, 4, 5)
        .collectionName(collectionName)
        .referenceParameter(left)
        .referenceParameter(right)
        .options(options)
        .validateAndReject()

      return this.trace('isReferenced', {
        collectionName,
        relationshipAttribute,
      })(async (env) => {
        const { isReferenced } = await this.client.isReferencedDataItem({
          ...env,
          dataCollectionId: collectionName,
          referringItemFieldName: relationshipAttribute,
          referringItemId: itemId(left),
          referencedItemId: itemId(right),
          ...toReadOptions(options),
          consistentRead: options?.consistentRead,
        })
        return isReferenced!
      })
    }
  )

  private trace(
    action: string,
    opts: object
  ): <R>(
    fn: (env: {
      environment?: apiTypes.Environment
      appId?: string
    }) => Promise<R>
  ) => Promise<R> {
    return async (fn) => {
      const gridAppId = await get(this.gridAppId)
      const env = { appId: gridAppId, environment: this.environment }
      if (this.tracer) {
        return this.tracer(`WixDataApi.${action}`, {
          ...opts,
          gridAppId,
          environment: this.environment,
        })(() => fn(env))
      }
      return fn(env)
    }
  }
}

function withArgs<Args extends any[], R>(
  fn: (all: IArguments, ...args: Args) => R
): (...args: Args) => R {
  return function (...args) {
    return fn(arguments, ...args)
  }
}

function toPatchOptions(options?: WixDataPatchOptions) {
  return {
    suppressHooks: options?.suppressHooks,
    ...draftsOptions(options?.showDrafts),
  }
}

function toUpdateOptions(options?: WixDataUpdateOptions) {
  return {
    ...toOptions(options),
    includeReferencedItems: options?.includeReferences,
  }
}

function toOptions(opts?: WixDataOptions) {
  if (!opts) {
    return undefined
  }
  const res = {
    suppressHooks: opts.suppressHooks,
    appOptions: opts.appOptions,
    ...draftsOptions(opts?.showDrafts),
  }
  if (Object.entries(res).every((kv) => kv[1] === undefined)) {
    return undefined
  }
  return res
}

function toReadOptions(options?: WixDataReadOptions) {
  return {
    suppressHooks: options?.suppressHooks,
    appOptions: options?.appOptions,
    ...draftsOptions(options?.showDrafts),
    consistentRead: options?.consistentRead,
    language: options?.language,
  }
}

function draftsOptions(showDrafts?: boolean) {
  return showDrafts !== undefined
    ? { publishPluginOptions: { includeDraftItems: showDrafts } }
    : {}
}

function toDataItem(item: apiTypes.DataItem): WixDataItem {
  return item.data as WixDataItem
}

function toPaging(limit: number | undefined, cursorOrOffset: string | number) {
  return typeof cursorOrOffset === 'string'
    ? {
        cursorPaging: {
          limit,
          cursor: cursorOrOffset,
        },
      }
    : limit !== undefined || cursorOrOffset > 0
    ? {
        paging: {
          limit,
          offset: cursorOrOffset,
        },
      }
    : {}
}

function toSort(orderBy: QueryParams['orderBy']): apiTypes.Sorting[] {
  return orderBy.map((fieldOrder) => {
    const [[fieldName, sort]] = Object.entries(fieldOrder)
    const order =
      sort === 'asc'
        ? apiTypes.SortOrder.ASC
        : sort === 'desc'
        ? apiTypes.SortOrder.DESC
        : undefined
    return { fieldName, order }
  })
}

async function get<T>(providerOrValue: T | Provider<T>): Promise<T> {
  if (typeof (providerOrValue as any)?.get === 'function') {
    return (providerOrValue as Provider<T>).get()
  }
  return providerOrValue as T
}

function recover<T>(code: string, value: T): (error: unknown) => Promise<T> {
  return (error) => {
    if (
      error instanceof Error &&
      (error as any)?.details?.applicationError?.code === code
    ) {
      return Promise.resolve(value)
    }
    return Promise.reject(error)
  }
}

function warnAboutBrokenFields(item: Partial<WixDataItem>) {
  const hasFieldWithDollar = hasField(item, (key) => key.startsWith('$'))
  const hasFieldWithDot = hasField(item, (key) => key.includes('.'))
  if (hasFieldWithDollar) {
    console.error(
      "Data inserted contains field name prefixed with symbol '$'. It is not recommended to use such field names as it will not work with some data operations."
    )
  }
  if (hasFieldWithDot) {
    console.warn(
      "Data inserted contains field name containing symbol '.'. It is not recommended to use such field names as it will not work with some data operations."
    )
  }
}

function toBulkResult(
  request: any[],
  results: apiTypes.BulkDataItemResult[],
  ignoreCodes: string[] = []
): WixDataBulkResult {
  const errors = results.flatMap((r) => {
    if (r.itemMetadata?.success || !r.itemMetadata?.error) {
      return []
    }
    if (ignoreCodes.includes(r.itemMetadata.error.code ?? '')) {
      return []
    }
    return [
      new BulkError(
        r.itemMetadata.error.description!,
        r.itemMetadata.error.code!,
        request[r.itemMetadata.originalIndex!],
        'BulkError',
        r.itemMetadata.originalIndex!
      ),
    ]
  })
  const success = results.filter((r) => r.itemMetadata?.success)
  const inserted = success.filter(
    (r) => r.action === apiTypes.BulkActionType.INSERT
  )
  const updated = success.filter(
    (r) =>
      r.action === apiTypes.BulkActionType.UPDATE ||
      r.action === apiTypes.BulkActionType.PATCH
  )
  const removed = success.filter(
    (r) => r.action === apiTypes.BulkActionType.DELETE
  )
  return {
    inserted: inserted.length,
    updated: updated.length,
    removed: removed.length,
    skipped: request.length - errors.length - success.length,
    errors,
    insertedItemIds: inserted.map((r) => r.itemMetadata?.id!),
    updatedItemIds: updated.map((r) => r.itemMetadata?.id!),
    removedItemIds: removed.map((r) => r.itemMetadata?.id!),
  }
}

function unwrapAggregationId(
  items: Record<string, any>[]
): Record<string, any>[] {
  // When grouping by one field WixDataService returns value on _id field directly, DataItemService wraps it in an object, i.e.
  // grouping by surname returns {_id: 'Simpson'} in WixDataService vs {_id: {surname: 'Simpson'}} in DataItemService
  // When grouping by more fields, values are kept wrapped.
  return items.map((item) => {
    const id = item._id
    if (isObject(id)) {
      const [key, ...rest] = Object.keys(id)
      if (rest.length === 0) {
        item._id = id[key]
        return item
      }
    }
    return item
  })
}

function onlyReferencedItems(
  results: apiTypes.ReferencedResult[]
): WixDataItem[] {
  return results
    .map((result) => result.dataItem)
    .filter(notEmpty)
    .map(toDataItem)
}

function notEmpty<T>(value: T | null | undefined): value is T {
  return value != null
}

function itemId(item: WixDataItemOrId): string {
  return typeof item === 'string' ? item : item._id
}

function asArray<T>(xOrXs: T | T[]): T[] {
  return isArray(xOrXs) ? (xOrXs as T[]) : [xOrXs as T]
}

function isOffset(cursorOrOffset: string | number) {
  return typeof cursorOrOffset === 'number'
}

const ItemDoesNotExistCode = 'WDE0073'

function apiValidator() {
  return commonApiValidator(wdeValidationError)
}
